.Footer
{
    background: var(--color-low)!important;
    color: var(--color-high);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2rem;

    padding: 6rem 0rem 1.5rem 0rem;
}

.footer-top
{
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
    width: 80%;
    gap: 1rem;

}

.footer-top h3
{
    margin-bottom: 1rem;
}

.footer-top a:hover
{
    color: var(--color-blue);
}

.footer-logo img {
  width: auto;
  height: 50px;
  object-fit: cover;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}


hr
{
    background-color: var(--color-lower);
    width: 80%;
}