.navbar {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
  padding: 1.5rem 2rem 1rem 1.5rem;


  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 3;
  background: var(--color-low);
}

.navbar__logo {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.navbar__logo img {
  width: auto;
  height: 50px;
  object-fit: cover;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

/*.navbar__links nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 1rem;
} */

.call-book-btn {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0.75rem 1rem;

  background: var(--color-high);
  color: var(--color-low);

  border-radius: 12px;
  border: 2px solid var(--color-high);

  font-weight: 400;
  font-size: 1rem;

  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);

  cursor: pointer;
}

.call-book-btn:hover {
  color: var(--color-blue);
}

@media screen and (max-width: 2560px) and (min-width: 1500px) {
  .navbar {
    padding: 3rem 4rem 2.5rem 3rem;
  }
  .navbar__logo img
  {
    height: 100px;
  }
  .call-book-btn 
  {
    padding: 1.5rem 2rem;
    font-size: 2rem;
    box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.5);
  }
}

@media screen and (max-width: 1024px) {
}

@media screen and (max-width: 768px) {
    .navbar {
        padding: 1.25rem 2rem 1rem 1.5rem;
    }
    .navbar__logo img
    {
        height: 42.5px;
    }
    .call-book-btn 
    {
        font-size: 0.75rem;
    }
}

@media screen and (max-width: 550px) {
    .navbar {
        padding: 1.25rem 1.5rem 1rem 1rem;
    }
}

@media screen and (max-width: 426px) {
    .navbar {
        padding: 1.25rem 1.25rem 1rem 0.75rem;
    }
    .navbar__logo img
    {
        height: 35px;
    
    }
    .call-book-btn 
    {
        padding: 0.5rem 0.75rem;
        font-size: 0.7rem;
        border-radius: 5px;
    }
}


