.Hero {
  height: 110vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 2rem;
  
}

.hero-title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 1rem;
}
.hero-title h1 {
  font-size: 3rem;
  font-weight: 800;
  color: var(--color-dark);
  text-align: left;
  text-transform: capitalize;
}

.hero-title p {
  font-size: 1.5rem;
  font-weight: 400;
  color: var(--color-dark);
  text-align: left;
  text-transform: capitalize;
}

.hero-vsl {
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 10px 10px 15px #888888;
  border-radius: 55px;
}

.hero-vsl iframe {
  width: 828px;
  height: 450px;
  border-radius: 55px;
  border: 5px solid var(--color-dark);
}

.hero-button button {
  padding: 1rem 2rem;
  font-size: 1rem;
  border: 5px solid var(--color-dark);
  border-radius: 50px;
}
.blue {
  color: var(--color-blue);
}
.sog_hero-static-text {
}
.sog_hero-dynamic-text {
}
.sog_hero-dynamic-text:after {
  content: "|";
  color: black;
  right: -0.15em;
  font-size: 1.2em;
  font-weight: 300;
  animation: blink-animation 1s steps(5, start) infinite;
  -webkit-animation: blink-animation 1s steps(5, start) infinite;
}

@keyframes blink-animation {
  to {
    visibility: hidden;
  }
}
@-webkit-keyframes blink-animation {
  to {
    visibility: hidden;
  }
}

@media screen and (max-width: 2560px) and (min-width: 1600px) {
  .hero {
    padding: 6rem 10rem;
    height: var(--long);
  }

  .hero-title h1 {
    font-size: 5.5rem;
  }
  .hero-title p {
    font-size: 3rem;
  }
  .hero-vsl iframe {
    width: 1428px;
    height: 775px;
  }
  .hero-button button {
    padding: 2rem 4rem;
    font-size: 2rem;
  }
}

@media screen and (max-width: 1024px) {
  .hero {
    padding: 4rem 0rem;
  }
  .hero-vsl iframe {
    width: 736px;
    height: 400px;
  }
  .hero-title h1 {
    font-size: 2.65rem;
  }
  .hero-title p {
    font-size: 1.25rem;
  }
}

@media screen and (max-width: 800px) {
  .hero-vsl iframe {
    width: 644px;
    height: 350px;
  }
  .hero-title h1 {
    font-size: 2rem;
  }
  .hero-title p {
    font-size: 1rem;
  }
}

@media screen and (max-width: 550px) {
  .hero {
    gap: 2rem;
  }
  .hero-vsl iframe {
    width: 368px;
    height: 200px;
    border-radius: 20px;
  }
  .hero-title {
    gap: 0.5rem;
  }
  .hero-title h1 {
    font-size: 1.7rem;
  }
  .hero-title p {
    font-size: 1.2rem;
  }
  .hero-button button {
    padding: 0.65rem 1.65rem;
    font-size: 1rem;
    border-radius: 45px;
  }
}

@media screen and (max-width: 430px) {
  .hero {
    padding: 2rem 1rem;
    gap: 2rem;
  }
  .hero-vsl iframe {
    width: 310px;
    height: 170px;
    border-radius: 20px;
    border: 3px solid var(--color-dark);
    font-size: 1rem;
  }
  .hero-title h1 {
    font-size: 1.3rem;
  }
  .hero-title p {
    font-size: 1rem;
  }
  .hero-button button {
    padding: 0.5rem 1rem;
    font-size: 0.8rem;
    border-radius: 20px;
  }
}

@media screen and (max-width: 400px) {
  .hero-vsl iframe {
    width: 320px;
    height: 170px;
    border-radius: 20px;
    border: 3px solid var(--color-dark);
    font-size: 1rem;
  }
  .hero-title h1 {
    font-size: 1.15rem;
  }
  .hero-title p {
    font-size: 0.9rem;
  }
}

@media screen and (max-width: 350px) {
  .hero-vsl iframe {
    width: 280px;
    height: 150px;
    border-radius: 20px;
    border: 3px solid var(--color-dark);
    font-size: 0.9rem;
  }
  .hero-title h1 {
    font-size: 1rem;
  }
  .hero-title p {
    font-size: 0.85rem;
  }
  .hero-button button {
    padding: 0.5rem 1rem;
    font-size: 0.8rem;
    border-radius: 20px;
  }
}
