.learn-more a:hover
{       
    color: var(--color-blue);
    font-weight: 800;
}

.learn-more a
{
    color: var(--color-dark);
    font-weight: 600;
    text-decoration: none;
    transition: all 0.3s ease;
    display: flex;
    justify-content: center;
    align-items: center;
}

.icon
{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
}