.about {
  background: var(--color-high);
  color: var(--color-low);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  text-align: left;
  gap: 2rem;
}

.about__content p {
  text-align: left;
  line-height: 1.75rem;
  font-size: 1.25rem;
}

.call-book-btn-white {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0.75rem 1rem;

  background: var(--color-low);
  color: var(--color-high);

  border-radius: 12px;
  border: 2px solid var(--color-low);

  font-weight: 600;
  font-size: 1rem;

  box-shadow: 5px 5px 5px rgba(211, 211, 211, 0.2);

  cursor: pointer;
}

.call-book-btn-white:hover {
  color: var(--color-darkblue);
}
